@import url(https://fonts.googleapis.com/css?family=Fredoka+One&display=swap);
h1, .brand-logo {
  font-family: 'Fredoka One', cursive;
}

.card .card-content {
  padding: 24px 24px 24px 14px;
}

nav ul a {
  font-size: 1.2rem;
}

a {
  color: #c2185b;
}

.input-field {
  margin-top: 2rem;
}

.input-field .prefix.active {
   color: #000;
 }

input.pink-input {
  border-bottom: 1px solid #f48fb1!important;
  box-shadow: 0 1px 0 0 #f48fb1!important;
}

input.pink-input + label {
  color: #f48fb1!important;
}

input.pink-input:focus + label {
  color: #c2185b!important;
}

input.pink-input:focus {
  border-bottom: 1px solid #c2185b!important;
  box-shadow: 0 1px 0 0 #c2185b!important;
}

tbody > tr:hover {
  transition: 0.3s ease;
  cursor: pointer;
  background: #ffedf3;
}

tbody > tr.checked {
  background: #ffedf3;
}

th {
  text-align: center;
}

td.Active {
  color: rgb(38, 166, 154);
}

td.Blocked {
  color: #c2185b;
}

